import axios from "axios";
import { initializeApp } from "firebase/app"
import { getFirestore, collection, getDocs } from "firebase/firestore"


const fetchSmartContractData = () =>
    async function getSmartContractData(address: string) {
        try {
            const firebaseApp = initializeApp({
                apiKey: "AIzaSyAoCQ_chvQfO4CAEqULXqgZbRAlsrtv3ms",
                authDomain: "sideproject-caching.firebaseapp.com",
                projectId: "sideproject-caching",
              });
            const db = getFirestore();
            const querySnapshot = await getDocs(collection(db, "smart contracts"));
            
            let doc = querySnapshot.docs.filter(d => d.id === address)
            if(doc.length === 0)
              return undefined;
            return doc[0].data();

        } catch (err) {
            return {
                success: false,
            };
        }
    };

export const getSmartContractData = fetchSmartContractData();