import { routeNames } from "../../routes";
import * as Dapp from "@elrondnetwork/dapp";
import React from "react";
import { useHistory } from "react-router-dom";
import useNewTransaction from "../Transaction/useNewTransaction";
import { smartContractAddress, smartContractInitialCount } from "../../config";
import { fetchQueryHex } from "../../helpers/sqQueries";
import { RawTransactionType, PaymentInfo } from "../../helpers/types";
import { Address, AddressValue, TokenIdentifierValue, U8Value } from "@elrondnetwork/erdjs/out";
import { int2hex, str2hex } from "../../helpers/encoding";
import { getAccountDetails, getWalletNFTs } from "../../helpers/asyncRequests";
import { getSmartContractData } from "../../helpers/firestoreQuery";
import BigNumber from "bignumber.js"

const Home = () => {
  const max = 7;
  const min = 1;
  const [selectedPaymentData, setSelectedPaymentData] = React.useState<PaymentInfo>();
  const [paymentData, setPaymentData] = React.useState<PaymentInfo[]>([]);
  const [remainingSupply, setRemainingSupply] = React.useState<number>(0);
  const [didMount, setDidMount] = React.useState<boolean>(false);
  // const [mintPrice, setMintPrice] = React.useState<number>(0);
  const [quantity, setQuantity] = React.useState<number>(min);
  const { loggedIn, address } = Dapp.useContext();
  const send = Dapp.useSendTransaction();
  const newTransaction = useNewTransaction();
  const history = useHistory();
  const dappLogout = Dapp.useLogout();

  const logOut = (e: React.MouseEvent) => {
    e.preventDefault();
    dappLogout({ callbackUrl: `${window.location.origin}${routeNames.home}` });
    history.push(routeNames.walletconnect);
  };

  const computePriceByQty = (payment: PaymentInfo) => {
    return parseFloat((quantity * payment.contractPrice).toFixed(3));
  }

  const buyNftTransaction: RawTransactionType = {
    receiver: smartContractAddress,
    value: "0",// computePriceByQty().toString(),
    gasLimit: 20_000000 + (95_000000 * quantity),
    data: ``
  };

  const setTransactionData = () => {
    if (!selectedPaymentData)
      return;
    switch (selectedPaymentData?.tokenDisplayName) {
      case "EGLD":
        buyNftTransaction.value = parseFloat((selectedPaymentData.contractPrice * quantity).toFixed(3)).toString();//(quantity * (selectedPaymentData?.contractPrice)).toString();
        buyNftTransaction.data = `buyNft@01@${int2hex(quantity)}`;
        buyNftTransaction.receiver = smartContractAddress;
        // buyNftTransaction.gasLimit = 600_000_000; //110_000000 + ((quantity - 1) * 85_000000);
        buyNftTransaction.gasLimit = 10_000_000 + ((quantity - 1) * 3_000_000);
        break;
    }
  }

  const handleButtonClick = (e: React.MouseEvent) => {
    e.preventDefault();
    if (e.currentTarget.classList.contains("disabled"))
      return;
    if (!loggedIn) {
      history.push(routeNames.walletconnect);
    }
    else {
      setTransactionData();
      let transaction = newTransaction(buyNftTransaction);
      send({
        transaction: transaction,
        callbackRoute: routeNames.transaction
      });
    }
  }

  const increment = (e: React.MouseEvent) => {
    e.preventDefault();
    if (e.currentTarget.classList.contains("disabled"))
      return;
    if (quantity == max || quantity == remainingSupply) {
      return;
    }

    setQuantity(quantity + 1);
  }

  const decrement = (e: React.MouseEvent) => {
    e.preventDefault();
    if (e.currentTarget.classList.contains("disabled"))
      return;
    if (quantity == min) {
      return;
    }

    setQuantity(quantity - 1);
  }

  const init = () => {
    if (didMount)
      return;

    getSmartContractData(smartContractAddress).then(response => {
      if (response === undefined)
        return;
      setRemainingSupply(parseInt(response.supply, 16) || 0)
    });
    if (!loggedIn)
      return;
    Promise.all([
      getSmartContractData(smartContractAddress),
      getAccountDetails(address),
    ]).then(([
      scData,
      accountDetails,
    ]) => {
      let egldPrice = parsePrice(scData?.EGLD);
      let pd = [];
      let paymentInfo: PaymentInfo = {
        tokenDisplayName: "EGLD",
        tokenTicker: "",
        tokenNonce: "00",
        userBalance: parseInt(accountDetails.data.balance) / 1_000_000_000_000_000_000,
        contractPrice: egldPrice
      };
      pd.push(paymentInfo);
      setSelectedPaymentData(pd[0]);
      setPaymentData(pd);
    });
    setDidMount(true);
  }

  React.useEffect(() => {
    init();
  }, [loggedIn]);

  const parsePrice = (val: string) => {
    if (val == "-1") {
      return 0;
    }
    else {
      let price = parseInt(val, 16);
      return price / 1_000_000_000_000_000_000;
    }
  }

  const paymentComponent = () => selectedPaymentData && <>
    <div className="d-flex justify-content-center mt-2 mb-5">
      <h6 style={{fontSize: "30px"}}>Buy price: {computePriceByQty(selectedPaymentData)} {selectedPaymentData.tokenDisplayName}</h6>
    </div>

    <div className="d-flex justify-content-center mt-3">
      <h6 style={{fontSize: "28px"}}>Quantity:</h6>
    </div>
    <div className="d-flex justify-content-center mt-3">
      <div className="d-flex justify-content-center mt-3">
        <button style={{paddingLeft: "20px", paddingRight: "21px"}} className={"btn btn-danger mr-2" + (remainingSupply > 0 ? "" : " disabled")} onClick={decrement}>
          &mdash;
        </button>
        {/* <input className="quantity-input__screen ml-2 mr-2" type="text" value={quantity} readOnly style={{ maxWidth: "55px", textAlign: "center", border: "none", fontSize: "24px" }} /> */}
        <p className="quantity-input__screen ml-4 mr-4" style={{ maxWidth: "55px", textAlign: "center", border: "none", fontSize: "24px" }}>{quantity}</p>
        <button style={{padding: "15px"}} className={"btn btn-success ml-2" + (remainingSupply > 0 ? "" : " disabled")} onClick={increment}>
          &#xff0b;
        </button>
      </div>
    </div>
    <div className="d-flex justify-content-center mt-5 mb-4">
      <a href="#" style={{fontSize: "32px"}} className={"btn default-button" + (remainingSupply > 0 ? "" : " disabled")} onClick={(e) => handleButtonClick(e)}>Buy NFT</a>
    </div>
    <div className="d-flex justify-content-center mb-5">
      <a href="#" className={"btn btn-sm default-button-danger"} onClick={(e) => logOut(e)}>Disconnect</a>
    </div>
  </>;

  return (
    <div className="container">
      <div className="row">
        <div className="col-lg-12 col-sm-12">
          <div className="d-flex justify-content-center mt-5 mb-5">
            <h5 style={{fontSize: "60px"}}>Alien Punks</h5>
          </div>
          <div className="d-flex justify-content-center mt-3">
            <h6 style={{fontSize: "34px"}}>Remaining supply: {remainingSupply}/{smartContractInitialCount}</h6>
          </div>
          {loggedIn && paymentData.length > 0 && paymentComponent()}
          {!loggedIn && <>
            <div className="d-flex justify-content-center mt-5 mb-5">
              <a href="#" className={"btn default-button" + (remainingSupply > 0 ? "" : " disabled")} onClick={(e) => handleButtonClick(e)}>Connect with Maiar</a>
            </div>
          </>}
        </div>
      </div>
    </div>
  );
};

export default Home;
