import * as React from "react";
import * as Dapp from "@elrondnetwork/dapp";
import { faCheck, faTimes } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useLocation, Link } from "react-router-dom";
import PageState from "../../components/PageState";
import { routeNames } from "../../routes";

const Transaction = () => {
  const { search } = useLocation();
  const { explorerAddress } = Dapp.useContext();

  const query = new URLSearchParams(search);
  const { status, txHash } = Object.fromEntries(query);

  return status === "success" ? (
    <PageState
      svgComponent={
        <FontAwesomeIcon icon={faCheck} className="text-success fa-3x" />
      }
      className="dapp-icon icon-medium"
      title="Success"
      description={
        <>
          <p>
            <a
              href={`${explorerAddress}transactions/${txHash}`}
              {...{
                target: "_blank",
              }}
              className="btn btn-success mt-3"
              title="View in Explorer"
              style={{fontFamily: "Audiowide"}}
            >
              Transaction details
            </a>
          </p>
          <Link to={routeNames.home} className="btn btn-danger mt-3" style={{fontFamily: "Audiowide"}}>
            Back
          </Link>
        </>
      }
    />
  ) : (
    <PageState
      svgComponent={
        <FontAwesomeIcon icon={faTimes} className="text-danger fa-3x" />
      }
      className="dapp-icon icon-medium"
      title="Error"
      description={
        <>
          <p>
            <a
              href={`${explorerAddress}transactions/${txHash}`}
              {...{
                target: "_blank",
              }}
              className="btn btn-success mt-3"
              title="View in Explorer"
              style={{fontFamily: "Audiowide"}}
            >
              Transaction details
            </a>
          </p>
          <Link to={routeNames.home} className="btn btn-danger mt-3" style={{fontFamily: "Audiowide"}}>
            Back
          </Link>
        </>
      }
    />
  );
};

export default Transaction;
