import * as React from "react";

const PageState = ({
  title,
  description,
  svgComponent,
  className,
  spin = false,
}: {
  title?: string;
  description?: string | React.ReactNode;
  svgComponent: React.ReactNode;
  className?: string;
  spin?: boolean;
}) => {
  return (
    <div className="d-flex flex-fill justify-content-center align-items-center container page-state" style={{fontFamily: "Amiko"}}>
      <div className="row w-100 mt-5 mb-5">
        {spin ? (
          <div className="col-12 text-center d-flex justify-content-center" data-testid="loader">
            <div className="lds-ellipsis mx-auto mt-5 mb-5">
              <div />
            </div>
          </div>
        ) : (
          <div className="col-12 mx-auto">
              <div className="text-center d-flex flex-column justify-content-center">
                <div className={`icon ${className ? className : ""}`}>
                  {svgComponent}
                </div>
                {title && <p className="h4 mt-3">{title}</p>}
                {description && <div className="mt-3">{description}</div>}
              </div>
            </div>
        )}
      </div>
    </div>
  );
};

export default PageState;
